
import loading from '@/components/loading/dataLoading';
import Scroller from '@/components/scroller_me/scroller';
import {setTitle} from '@/common/js/common';
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'


const baseList = [{
  url: 'javascript:',
  img: 'http://pic.qiantucdn.com/58pic/14/59/28/74A58PICIrd_1024.jpg',
  title: ''
}, {
  url: 'javascript:',
  img: 'http://pic.qiantucdn.com/58pic/23/12/64/92G58PIC93T_1024.jpg',
  title: ''
}, {
  url: 'javascript:',
  img: 'http://pic.qiantucdn.com/58pic/25/94/22/94558PICxJ3_1024.jpg',
  title: ''
}];

const urlList = baseList.map((item, index) => ({
  url: 'http://m.baidu.com',
  img: item.img,
  title: `(图片可点击)${item.title}`
}));

export default{
    data(){
        return{
          loading:true,
          demo06_list:urlList,
          demo06_index:0,
          ac_show:false,
          fruit:'banana',
          currentEstate:'重庆天地雍江艺庭',
          currentHouse:'1705房',
          options:[],
          this_options:[]
        }
    },
    components:{
        loading,Swiper,Cell,Actionsheet
    },
    methods:{
      demo06_onIndexChange (index) {
        this.demo06_index = index
      },
      showActionsheet(){
        this.ac_show=true;
      },
      click_option(key){
        for(let i in this.this_options){
          if((parseInt(i)+1)==key){        //item 表示Json串中的属性，如'name'
            let jValue=this.this_options[i];//key所对应的value
            this.currentEstate=jValue.estate;
            this.currentHouse=jValue.house;
          }
        }
      }
    },
    computed:{
      imageurl(){
        return {"width":(document.body.clientWidth-5)/2+"px","height":(document.body.clientWidth-5)/4+"px"}
      },
      imageradius(){
          return {
              "width":(document.body.clientWidth)*0.356+"px",
              "height":(document.body.clientWidth)*0.356+"px",
              "margin-top":-((document.body.clientWidth)*0.356+8)/2-2+"px",
              "margin-left":-((document.body.clientWidth)*0.356+8)/2+"px"
          }
      },
      fkyqStyle(){
        return{
            "width":25*(document.body.clientWidth)/414+"px",
            "height":35*(document.body.clientWidth)/414+"px",
            "left":56.5*(document.body.clientWidth)/414+"px",
            "top":17*(document.body.clientWidth)/414+"px",
        }
      },
      fkyqDiv(){
        return{
          "left":36*(document.body.clientWidth)/414+"px",
          "top":60*(document.body.clientWidth)/414+"px",
        }
      },
      bsbxStyle(){
        return{
          "width":35*(document.body.clientWidth)/414+"px",
          "height":35*(document.body.clientWidth)/414+"px",
          "right":51.5*(document.body.clientWidth)/414+"px",
          "top":17*(document.body.clientWidth)/414+"px",
        }
      },
      bsbxDiv(){
        return{
          "right":40*(document.body.clientWidth)/414+"px",
          "top":60*(document.body.clientWidth)/414+"px",
        }
      },
      pzshStyle(){
        return{
          "width":39.5*(document.body.clientWidth)/414+"px",
          "height":35*(document.body.clientWidth)/414+"px",
          "right":49.5*(document.body.clientWidth)/414+"px",
          "top":17*(document.body.clientWidth)/414+"px",
        }
      },
      jfStyle(){
        return{
          "width":47*(document.body.clientWidth)/414+"px",
          "height":35*(document.body.clientWidth)/414+"px",
          "left":45.5*(document.body.clientWidth)/414+"px",
          "top":17*(document.body.clientWidth)/414+"px",
        }
      },
      jfDiv(){
        return{
          "left":54.5*(document.body.clientWidth)/414+"px",
          "top":60*(document.body.clientWidth)/414+"px",
        }
      },
      smkmStyle(){
        return{
          "width":35.5*(document.body.clientWidth)/414+"px",
          "height":35*(document.body.clientWidth)/414+"px",
          "left":58*(document.body.clientWidth)/414+"px",
          "top":40*(document.body.clientWidth)/414+"px",
        }
      },
      smkmDiv(){
        return{
          "left":40*(document.body.clientWidth)/414+"px",
          "top":87*(document.body.clientWidth)/414+"px",
        }
      }
    },
    mounted(){
      let self = this;
      this.$http.get('/api/qj_index').then((data) => {
        let this_options = data.body.data.result.options;
        let json_options={};
        for(let i =0;i<this_options.length;i++){
          json_options[this_options[i].key]=this_options[i].estate+this_options[i].house;
        }
        self.options=json_options;
        self.this_options=this_options;
      });
        this.loading=false;
    }

}



